.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content {
  text-align: center;
  max-width: 800px;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

body { font-size: 16px; }
input, select, textarea { font-size: 100%; }

text {
  font-family: "Lucida Console", Monaco, monospace;  
  font-size: 12px;
}
